"use client";

import React, { useState } from "react";
import { useDeleteJob, useJobsforAdmin } from "@/app/customHooks/useJobs";
import { JobApplication, JobWithApplications } from "@/app/utility/props";
import JobForm from "./JobForm";
import { useRouter } from "next/navigation";

export default function JobsList() {
  const { data, isLoading, error } = useJobsforAdmin();
  const jobs = data as JobApplication[];
  const { mutate: deleteJob } = useDeleteJob();
  const [showForm, setShowForm] = useState(false);
  const router = useRouter();

  if (isLoading) return <p>Loading jobs...</p>;
  if (error) return <p>Failed to load jobs.</p>;

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-semibold text-gray-800">Job Openings</h2>
        <button
          onClick={() => setShowForm(true)}
          className="bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition"
        >
          + Post Job
        </button>
      </div>

      {showForm && <JobForm onClose={() => setShowForm(false)} />}

      <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
        {jobs?.map((job) => (
          <div
            key={job._id}
            className="bg-white border rounded-xl shadow-sm hover:shadow-lg p-5 transition"
          >
            <h3 className="text-lg font-semibold text-gray-800 mb-1">
              {job.title}
            </h3>
            <p className="text-gray-500 text-sm mb-2">{job.company}</p>
            <p className="text-gray-600 text-sm mb-3">{job.location}</p>
            <p className="text-gray-700 text-sm line-clamp-3 mb-4">
              {job.description}
            </p>
            <div className="flex justify-between items-center">
              <button
                onClick={() => router.push(`/admin/jobs/${job._id}/applications`)}
                className="text-blue-600 text-sm font-medium hover:underline"
              >
                View Applications ({job.applications.length})
              </button>
              <button
                onClick={() => deleteJob(job._id)}
                className="text-red-500 hover:text-red-700 text-sm font-medium"
              >
                Delete
              </button>
            </div>
          </div>
        ))}
      </div>

      {!jobs?.length && (
        <p className="text-gray-500 text-center mt-8">
          No job postings found. Add a new one!
        </p>
      )}
    </div>
  );
}
